.page {
  color: #060b35;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
}

.page__body {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__nav-list {
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header__nav-link:hover, .header .is-active {
  color: #00acdc;
}

.header .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  align-items: center;
  gap: 48px 46px;
  margin: 0;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 198px;
  height: 406px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 32px 19px 0;
  display: flex;
}

.card__descript {
  width: 166px;
  height: 36px;
  margin-bottom: 16px;
  padding: 40px 16px 0;
}

.card__descript-name {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__descript-name {
  color: #34568b;
}

.card__descript-code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  padding-top: 24px;
  display: flex;
}

.card__price-name {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  width: 166px;
  height: 40px;
  padding: 16px;
  display: block;
}

.card__buy-link {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  padding: 16px 16px 0;
  display: flex;
}

.stars__container {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-size: 16px 16px;
}

.stars__review {
  text-align: right;
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}
